import React, { Component } from 'react';
//import logo from './logo.svg';
import Content from '../Content/Content';
var Comm = require('../../service/comm.js');

class Watcher extends Component {
      constructor(props) {
        super(props);
        this.comm=new Comm();  
       /* this.state = {
            c_src:"https://www.youtube.com/embed/v_62AEeRzW8?autoplay=1&loop=1",
            c_type:"video",
            c_txt:"notxt",
        };*/
         /* this.state = {
            c_src:"https://static.pexels.com/photos/479/landscape-nature-sunset-trees.jpg",
            c_type:"img",
            c_txt:"notxt",
        };*/
          this.state = {
            c_src:"http://www.mescoloriages.com/coloriages/vie%20quotidienne/les%20transports/camions/images/camion12.gif",
            c_type:"nothing to docvwcxv",
            c_txt:"nothing to docvwcxv",
        };
        this.slidToDisplay=this.slidToDisplay.bind(this);
        this.comm.socketConnection(this.slidToDisplay);
}
    
slidToDisplay(msg){
        //msg.content.src,msg.slid.txt,"fade",500,msg.content.type
        console.log(msg);
                console.log("laaaa");

        console.log("msg.content.src:" +msg.src);
        console.log("msg.slid.txt:" +msg.title);
        console.log("msg.content.type:" +msg.type);
    this.setState({c_src:msg.src,c_type:msg.type,c_txt:msg.title});
}
    
    
  render() {
    return (
      <div className="Watcher">
        <Content src={this.state.c_src} type={this.state.c_type} txt={this.state.c_txt}/>
      </div>
    );
  }
}

export default Watcher;



